<template>
<scroll-view class="wrap">
  <div class="rank">
    <van-list finished-text="没有更多了" >
      <router-link v-for="(e, i) in topList" :key="i"
        tag="div" :to="{name: 'rank', params: {id: i + 1}}"
        class="topic">
        <img :src="e.picUrl" width="100" height="100">
        <div class="topic-info">
          <h3>{{e.topTitle}}</h3>
          <p v-for="(el, idx) in e.songList" :key="idx">
            {{idx + 1}}<span>{{el.singername}}</span>- {{el.songname}}
          </p>
          <van-icon name="arrow" color="#b4b4b4"/>
        </div>
      </router-link>
    </van-list>    
  </div>
</scroll-view>
</template>
<script>
import { mapGetters } from 'vuex'
import ScrollView from '#/Common/ScrollView'

export default {
  name: 'Rank',
  components: {
    ScrollView,
  },
  data() {
    return {
    }
  },
  computed: {
    ...mapGetters('music', ['topList'])
  },
  created() {
    this.$store.dispatch('music/rankList')
  }
}
</script>
<style lang="scss" scoped>
.wrap {
  height: 100vh;
}
.van-icon-arrow {
  position: absolute;
  right: 0;
  top: 50%;
  font-size: 14px;
  transform: translateY(-50%); }
.topic {
  position: relative;
  height: 2rem;
  margin: .2rem;
  background-color: #fff;
  img {
    position: absolute;
    top: 0;
    left: 0;
    height: 2rem;
    width: 2rem; }
  &-info {
    position: relative;
    margin-left: 2.3rem;
    margin-right: .3rem;
    h3 {
      font-size: 16px;
      padding: .1rem 0; }
    p {
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      font-size: 14px;
      color: #979797;
      padding: .1rem 0;
      span {
        font-weight: 400;
        margin-left: .1rem;
        color: #585858; } } } }
</style>


